                        <el-carousel-item
                            v-for="(item, i) in meta.pics"
                            :key="i"
                        >
                            <div class="m-face-pic">
                                <img
                                    v-photoswipe:single
                                    :src="item.url | showImage"
                                    class="u-pic"
                                />
                            </div>
                        </el-carousel-item>
                    </el-carousel>
                </div>
                <div class="m-single-data">
                    <el-divider content-position="left"
                        >Áã¨ÂÆ∂Êï∞ÊçÆÂàÜÊûê</el-divider
                    >
                    <facedata v-if="facedata" :data="facedata" />
                </div>
                <!-- <div class="m-single-actions">
                    <Like
                        class="u-like"
                        mode="heart"
                        :count="post.likes"
                        :showCount="true"
                        txt="ÂñúÊ¨¢"
                    />
                </div> -->
            </div>
            <div class="m-single-file" v-if="file">
                <el-table :data="downdata">
                    <el-table-column prop="author" label="Êï∞ÊçÆ‰ΩúËÄÖ">
                    </el-table-column>
                    <el-table-column prop="type" label="Êï∞ÊçÆ‰ΩìÂûã">
                    </el-table-column>
                    <el-table-column prop="file" label="Êï∞ÊçÆ‰∏ãËΩΩ">
                        <template slot-scope="scope">
                            <Down
                                classes="u-down el-button el-button--primary el-button--small"
                                :url="scope.row.file"
                                :showCount="true"
                                :count="stat.downs"
                            />
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <el-alert
                class="m-single-notice"
                title="ÁâπÂà´ËØ¥Êòé"
                type="warning"
                description="Êàë‰ª¨Â∞äÈáçÂíå‰øùÊä§Âéü‰ΩúËÄÖÁâàÊùÉÔºåÈÉ®ÂàÜ‰ΩúÂìÅÁî±ÁΩëÁ´ôÂõ¢ÈòüËá™Ë¥π‰ªéÊ∑òÂÆùË¥≠‰π∞‰∏ä‰º†Êó†Ê≥ï‰∏Ä‰∏ÄÁîÑÂà´Âéü‰ΩúÊòØÂê¶‰ªòË¥πÔºåÂ¶ÇÊúâ‰æµÊùÉÔºåËØ∑ËÅîÁ≥ªadmin@jx3box.comÔºåÊàë‰ª¨Â∞ÜÁ´ãÂç≥Âà†Èô§Ôºå‰∫¶Ê¨¢Ëøé‰ΩúËÄÖËá™Ë°å‰∏ä‰º†Êé®ÂπøËá™Â∑±‰ΩúÂìÅ„ÄÇ"
                show-icon
            >
            </el-alert>
    </singlebox>
</template>

<script>
import _ from 'lodash'
import singlebox from "@jx3box/jx3box-page/src/cms-single";
import {resolveImagePath} from '@jx3box/jx3box-common/js/utils'
import { getPost } from "../service/post.js";
import { getStat, postStat } from "../service/stat.js";
import facedata from "@jx3box/jx3box-facedat/src/Facedat.vue";

export default {
    name: "single",
    props: [],
    data: function() {
        return {
            loading: false,

            post: {},
            stat: {},
            meta: {},
            author: {},
        };
    },
    computed: {
        id: function() {
            return this.$store.state.pid;
        },
        post_subtype: function() {
            return _.get(this.post, "post_subtype") || '';
        },
        file: function() {
            return resolveImagePath(_.get(this.post.post_meta, "file"));
        },
        downdata: function() {
            return [
                {
                    author: _.get(this.post.post_meta, "author"),
                    file: this.file,
                    type: this.post_subtype,
                },
            ];
        },
        facedata: function() {
            return _.get(this.post.post_meta, "data");
        },
    },
    methods: {
        formatMeta: function(key) {
            let val = _.get(this.meta, key);
            if (Array.isArray(val)) {
                return val.toString();
            } else {
                return val;
            }
        },
    },
    filters: {
        showImage: function(val) {
            return resolveImagePath(val);
        },
    },
    created: function() {
        if (this.id) {
            this.loading = true;
            getPost(this.id, this)
                .then((res) => {
                    this.post = this.$store.state.post = res.data.data.post;
                    this.meta = this.$store.state.meta =
                        res.data.data.post.post_meta;
                    this.author = this.$store.state.author =
                        res.data.data.author;
                    this.$store.state.status = true;

                    // ÂàÜ‰∫´Êµ∑Êä•ÈáçÂÆö‰πâ
                    this.post.post_banner = this.meta.pics && this.meta.pics.length && this.meta.pics[0]['url']
                })
                .finally(() => {
                    this.loading = false;
                });

            getStat(this.id).then((data) => {
                if (data) this.stat = this.$store.state.stat = data;
            });
            postStat(this.id);
        }
    },
    components: {
        facedata,
        singlebox,
    },
};
</script>

